import React from 'react'
import { useTypewriter,Cursor } from 'react-simple-typewriter';


function Info  ({title, comment, links,youtube}) {
const [header] = useTypewriter({
  words:[title],
  loop:1,
  typeSpeed:60,
 
}) 
const [desc] = useTypewriter({
  words:[comment],
  loop:1,
  typeSpeed:50,
 
})  
  return (

    <div className="container">
        <div className="title">
            <ul>
                <li>{header}</li>
            </ul>
        </div>
        <div className="comment">
            {desc}{
              links ? <a href={links}>Allmylinks profile.</a> : ''
              
            }
        </div>
    </div>
  )
}

export default Info;